import React, { Component } from 'react';
import './MainContainer.css';
import TorneosContainer from '../TorneosContainer/TorneosContainer';
import PartidosContainer from '../PartidosContainer/PartidosContainer';
import InfoContainer from '../InfoContainer/InfoContainer';


class MainContainer extends Component {

    constructor(props) {
        super(props)
        this.state = {
            Torneos: [],
            Partidos: [],
            PartidoSelecionado:'',
            timeLineTop:1,
           }


    }

    componentWillMount() {
     
      this.updateData();
       
      
      
      }
     

      shouldComponentUpdate(){
          return true;
      }
      updateTime = () => {
        const {timeLineTop}=this.state;
        this.setState({
            timeLineTop: timeLineTop+1
        })
        console.log(timeLineTop)
     
      }
      componentDidMount() {
        this.timerID = setInterval(
          () => this.updateTime(),
          100000
        );
      }
    
      componentWillUnmount() {
        clearInterval(this.timerID);
      }
    
    

        updateData = () => {

            fetch('http://gmatchapp.com/api/v1/torneos')
            .then((response) => {
              return response.json()
            })
            .then((data) => {
              this.setState({ Torneos: data })
            })
        }
      onClick = (id) => {
 
        console.log(id)
        this.setState({ PartidoSelecionado: id })
      }

    render(){


        const {Torneos,PartidoSelecionado}=this.state;
        console.log(PartidoSelecionado);
        return (
            
		<div className="mainContainer">
          
               {/*<div className="col-md-3 padding0 border1">
                    <TorneosContainer Torneos={Torneos}/>
        </div>*/}
                <div className=" maincontainer2 padding0 ">
                    <PartidosContainer Torneos={Torneos} onClick={this.onClick}/>
                </div>
                {/*<div className="col-md-3 padding0 border3">
                    <InfoContainer partido={PartidoSelecionado}/>
      </div>*/}
            </div>
        );		

}
}
export default MainContainer;